<script>
  function isTencentWx() {
    var ua = navigator.userAgent.toLowerCase();
    return ua.match(/MicroMessenger/i) == "micromessenger";
  }

  let slogan = "A weekly newsletter of AR Development, AR apps and games.";
  let subscribed = new URLSearchParams(location.search).get("subscribed");

  let message = {
    pending: "Please check your email to confirm your subscription.",
    confirmed: "Thank you. Your subscription is now confirmed."
  }[subscribed];

  if (subscribed) {
    history.replaceState({}, "", "/");
  }

  let curatedAPI = isTencentWx()
    ? "http://curator.argoodies.com/email_subscribers"
    : "https://argoodies.curated.co/email_subscribers";
</script>

