  <p class="slogan">{slogan}</p>
</div>
{#if subscribed}
  <div class="message-wrap">{message}</div>
{:else}
  <div class="subscribe-form-root">
    <form
      action={curatedAPI}
      id="embedded-subscribe-form"
      class="subscribe-form"
      method="post">
      <input
        id="subscribe-email"
        placeholder="Your email address"
        class="left-radius email-input"
        name="email"
        type="email" />
      <input name="form_dom_id" value="embedded-subscribe-form" type="hidden" />
      <button
        type="submit"
        class="right-radius submit-button"
        onclick="this.form.submit(); this.disabled=true; this.value='SENDING';"
        value="SUBSCRIBE">
